// mixin

// boxSizing
* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
       -o-box-sizing: border-box;
      -ms-box-sizing: border-box;
}

// 
@mixin bg-cover {
  background-size: cover;
  background-position: center center;
}

// 
@mixin clearfix {
  clear: both;
}

// 
//  vertical-rl  vertical-lr 
@mixin text-vertical($direction) {
  writing-mode: $direction;
  -webkit-writing-mode: $direction;
}

// local mixin
// 
.bg-cover {
  background-size: cover;
  background-position: center center;
}

// 
.text-hide {
  text-indent: 101%;
  overflow: hidden;
  white-space: nowrap;
}

// checkout-process bar
//  $primary$light
@mixin checkout-process($color) {
  & {
    position: relative;
    width: $w100;

    &:after {
      content: "";
      position: absolute;
      top: 50%;
      bottom: 0;
      right: 0;
      left: 0;

      height: 1px;
      background-color: $color;
    }

    .checkout-completed {
      background-color: $color;
      .material-icons {
        //  icon 
        font-size: 1rem;
        border-radius: 50%;
      }
    }
  }
}
